.navbar {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

.navbar-page-container {
    padding: 20px;
}

.navbar-page-link {
    margin-right: 10px;
    text-decoration: None;
}

.navbar-page-button {
    font-family: "Lexend", sans-serif;
    border: none;
    padding: 0;
    box-shadow: inset 0 0 0 0 #E0AFA1;
    transition: ease-in-out 0.3s;
}

.navbar-page-button:hover {
    box-shadow: inset 0 100px 0 0 #E0AFA1;
}